

import goodsCard from '../../components/goodsCard/goodsCard.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
Flexbox,
FlexboxItem,
goodsCard,
ViewBox,
XHeader
},
data() {
return {
title: '',
goodsList: [{}],
}
},
mounted() {
console.log("传过来的标题", this.$route.params["title"])
this.title = this.$route.params["title"];
this.$get(
this.$serverHome + "goods/getList", {
page: this.currentPage,
limit: this.pageSize
},
res => {
console.log(res);
this.goodsList = res.msg;
}
);
},
methods: {}
}



